/* Block */
.breadcrumbs-section {
  padding-top: 70px;
  padding-bottom: 50px;
}
.breadcrumbs-section .breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item {
  position: relative;
  font-size: 14px;
  font-weight: 500;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item.active {
  color: #2B478B;
}
.breadcrumbs-section .breadcrumb a {
  color: #767676;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 30px;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "";
  position: absolute;
  top: calc(50% - 4px);
  left: calc(15px - 4px);
  width: 5px;
  height: 8px;
  background-image: url(../../assets/img/svg/angle-right.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .breadcrumbs-section {
    padding-top: 20px;
  }
}
.article-card img {
  margin-bottom: 30px;
}
.article-card .article-title {
  font-size: 24px;
  font-weight: 700;
}

@media (max-width: 991px) {
  .article-card img {
    width: 100%;
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  .article-card .article-title {
    font-size: 20px;
  }
}
/* Section */
.useful-links-section .row {
  margin: -10px;
}
.useful-links-section .col-link {
  padding: 10px;
}
.useful-links-section .col-link:nth-child(1) .link-wrap {
  background-color: #FFCC00;
}
.useful-links-section .col-link:nth-child(2) .link-wrap {
  background-color: #FED638;
}
.useful-links-section .col-link:nth-child(3) .link-wrap {
  background-color: #FFDC52;
}
.useful-links-section .col-link:nth-child(4) .link-wrap {
  background-color: #FFE98F;
}
.useful-links-section .col-link a:hover {
  text-decoration: none;
}
.useful-links-section .col-link a:hover .learn-more .svg-icon {
  transform: rotate(-180deg);
}
.useful-links-section .col-link .link-wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px 20px;
  border-radius: 15px;
}
.useful-links-section .col-link .link-wrap .h2 {
  margin-bottom: 20px;
}
.useful-links-section .col-link .link-wrap p {
  margin-bottom: 20px;
  font-size: 15px;
  font-weight: 500;
  flex: 1;
}
.useful-links-section .col-link .link-wrap .svg-icon {
  margin-right: 5px;
  transition: all 0.2s ease-in-out;
}
.useful-links-section .col-link .link-wrap .learn-more {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 600;
}

.blog-card-section .article-header {
  padding-top: 40px;
  padding-bottom: 40px;
  margin-bottom: 40px;
  position: relative;
}
.blog-card-section .article-header .blog-card-bg {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: -115px;
  bottom: 0;
  z-index: -1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.blog-card-section .article-header .date-block {
  display: flex;
  align-items: center;
}
.blog-card-section .article-header .date-block .date-icon,
.blog-card-section .article-header .date-block .date {
  margin-right: 15px;
}
.blog-card-section .article-description p {
  margin-bottom: 30px;
}
.blog-card-section .article-description p:last-child {
  margin-bottom: 0;
}

@media (min-width: 992px) {
  .blog-card-section .article-header .section-title {
    margin-bottom: 120px;
  }
}
@media (max-width: 991px) {
  .blog-card-section .article-header {
    padding-top: 0;
  }
}